.back-movie {
  position: absolute;
  left: 0;
  top: 20px;
  z-index: -999;
}

.map_border {
  fill: #aadaff;
  stroke: darkblue;
  stroke-width: .5;
  opacity: .0;
}

.graticule {
  fill: none;
  stroke: blue;
  stroke-width: .04;
  opacity: .1;
}

.tropics {
  fill: none;
  stroke: red;
  opacity: .5;
  stroke-width: .08;
}

.tropic0 {
  fill: none;
  stroke: red;
  opacity: .5;
  stroke-width: .08;
}

.land {
  fill: Tan;
  fill-opacity: 0.8;
  stroke: black;
}

.countries {
  fill: #ffedcd; 
//  fill-opacity: .9;
  stroke: black;
}

.countries path:hover {
  fill: pink;
  fill-opacity: .5;
  stroke: red;
  stroke-width: .05;
}

@mixin btnDemo($borderColor) {
  background-color: LightYellow;
  border: 4px solid $borderColor;
  color: black;
  padding: 4px 4px;
  font: small "Courier New", sans-serif;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
  margin: 4px 2px;
}

.btnDemo1 {
  @include btnDemo(#4caf50);
}

.btnDemo2 {
  @include btnDemo(Chocolate);
}

.report {
  font: small "Courier New", sans-serif;
  text-align: left;
  text-indent: 10px;
  line-height: 10px;
}
